
body {
    margin-left: 12%;
    margin-right: 12%;
    background-color: whitesmoke;
}
.Upperpart{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
}
.score {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.score h1 {
    font-size: 5rem;
    font-weight: 900;
    color: black;
    margin-bottom: 0;
}
.score h2 {
    margin-top: 0px;
    font-size: 1.5rem;
    font-weight: 600;

}
.options {
    display: flex;
    flex-direction: column;
    align-items: end;
}
.options h1 {
    font-size: 1.2rem;
    font-weight: 800;
}
.optionbtn {
    display: flex;
    gap: 20px;

}
.btn {
    background-color: white;
    padding: 13px 20px;
    border: 2px solid black;
    font-size: 1.3rem;
    outline: 0;
    font-weight: 700;
    cursor: pointer;
}
.btn:hover {
    background-color: black;
    color: white;
}
.mid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}
.mid img{
    width: 200px;
    height: 200px;
}
.mid button {
    width: 180px;
    height:40px;
    padding: 10px 10px;
    text-align: center;
    border-radius: 5px;
    outline: none;
}
.Clearbtn {
    background-color: white;
    border: 2px solid black;
    font-size: 1.1rem;
    font-weight: 400;
    margin-top: 20px;
    cursor: pointer;
}
.Clearbtn:hover {
    background-color: black;
    color: white;
}
.Showbtn {
    background-color: black;
    color: white;
    border: 2px solid black;
    font-size: 1.1rem;
    font-weight: 400;
    margin-top: 20px;
    cursor: pointer;
}
.mid p{
    font-size: 1rem;
    color: black;
    font-weight: 400;
}
.btn .selected{
    background-color: black;
    color: white;
}