body {
    margin-left: 12%;
    background-color: whitesmoke;
    margin-right: 12%;
}
.Container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100vh;
}
.Left {
    width: 500px;
    height: 500px;
}
.Left img{
    width: 500px;
    height: 400px;
}
.Right {
    display: flex;
    flex-direction: column;
    align-items: end;
}
.Right h1 {
    font-size: 5rem;
    font-weight: 900;
    color: black;
    margin-top: -100px;
    margin-bottom: 0;
}
.Right button {
    font-size: 1.2rem;
    color: white;
    background-color: black;
    border: none;
    border-radius: 5px;
    padding: 10px 25px;
    margin-top: 20px;
    cursor: pointer;
}